import { createFeatureSelector, createSelector } from '@ngrx/store';
import { ObraSocial, Paciente } from '../../../shared/models/datos.models';
import { FormularioTurno } from '../../../shared/models/forms.models';
import { FiltrosPacientes } from '../../../shared/models/request.models';

export const selectFormularioTurno = createFeatureSelector<FormularioTurno>('formularioTurno');

// seleccion

// obras sociales
export const selectObraSocialSelected = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.obraSocialSelected
);
// turno
export const selectTurnoSelected = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.turnoSelected
);

// pacientes

export const selectPacientesFiltrados = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.pacientes
);

// paciente

export const selectFiltroPacientes = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => {
        const request = new FiltrosPacientes();
        if (formularioTurno.datosPaciente != undefined && 
                formularioTurno.datosPaciente.dni != undefined) {
            request.dni = formularioTurno.datosPaciente.dni.toString();
        }
        if (formularioTurno.datosPaciente != undefined && 
            formularioTurno.datosPaciente.nombreApellido != undefined) {
            request.apenom = formularioTurno.datosPaciente.nombreApellido.trim();
        }
    return request;
    }
);

export const selectPacienteSelected = createSelector(
    selectFormularioTurno,
    (formularioTurno: FormularioTurno) => formularioTurno.pacienteSelected
);