import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
// tslint:disable-next-line: max-line-length

import { Especialidad, EspecialidadProfesional, EspecialidadProfesionalBusq, Licencia, ObraSocial, Paciente } from '../../shared/models/datos.models';
import { FiltrosPacientes, FiltrosTurnos, Login } from '../../shared/models/request.models';
import { AlertasObraSocialRespuesta, EspecialidadProfesionalBusqRespuesta, EspecialidadProfesionalRespuesta, EspecialidadRespuesta, LicenciaRespuesta, LoginRespuesta, ObraSocialRespuesta, PacienteRespuesta, TurnoRespuesta } from '../../shared/models/responses.models';
import * as Mock from '../mocks/mocks';
import { getWsFromMock, throwErrorIfBadCode, throwErrorToUser } from '../utils/service.utils';
import { environment } from './../../../environments/environment';

@Injectable()
export class ServiceService {

  constructor (
    protected http: HttpClient
  ) { }

  useMockups = environment.mockups;
  endpoint = environment.endpoint;
  endpointC = this.endpoint + "/Consext" ;
  endpointG = this.endpoint + "/Gestion" ;
  endpointA = this.endpoint + "/Auth" ;

  endpoint_login = this.endpointA + '/Login';
  
  endpoint_obraSociales = this.endpointC + '/planesObrasSociales';
  endpoint_obraSocial = this.endpoint_obraSociales + '/'; // {codOS} 
  
  endpoint_especialidades = this.endpointC + '/especialidad';

  endpoint_especialidadesProfesional = this.endpointC + '/especialidadesProfesional';
  endpoint_licencias = this.endpointC + '/licencias/'; // ?ep={codProf} 
  endpoint_busquedaEspecialidadesProfesional = this.endpointC + '/busquedaProfesionales'; 
    '/busqueda'; // ?os={codOS}&esp={codEP}
  
  endpoint_turnos = this.endpointC + '/turnos'; // ?os={codOS}&ep={codEP}&fecha={YYYY-MM-DD}  

  endpoint_pacientes = this.endpointC + '/pacientes/busqueda/'; //?dni={DNI}&apenom={APENOM}';

  

  login(usuario: Login): Observable<any> {
    if (this.useMockups) {
      return getWsFromMock(Mock.tokenMock);
    } else {
      return this.http.post<LoginRespuesta>(this.endpoint_login, usuario)
        .pipe(map(
          (res: LoginRespuesta) => {
            if (res.token == undefined || res.token.length == 0) {
              throwErrorToUser(`Por favor intente más tarde.`);
            }
            return res.token;
          }
      ));
    }
  }

  getEspecialidades(): Observable<Especialidad[]> {
    if (this.useMockups) {
      return getWsFromMock(Mock.especialidadesMocks);
    } else {
      return this.http.get<EspecialidadRespuesta>(this.endpoint_especialidades)
        .pipe(map(
          (res: EspecialidadRespuesta) => {
            throwErrorIfBadCode(res);

            res.especialidad.forEach(element => {
              element.nombre = element.nombre.trim();
            });

            return res.especialidad.sort((a, b) => {
              if (a.nombre > b.nombre) return 1;
              if (a.nombre < b.nombre) return -1;
              return 0;
            });
          }
      ));
    }
  }

  getObrasSociales(): Observable<ObraSocial[]> {
    if (this.useMockups) {
      return getWsFromMock(Mock.obrasSocialesMocks);
    } else {
      return this.http.get<ObraSocialRespuesta>(this.endpoint_obraSociales)
        .pipe(map(
          (res: ObraSocialRespuesta) => {
            throwErrorIfBadCode(res);
            
            res.obraSocial.forEach(element => {
              if (element.nombre != undefined) {
                element.nombre = element.nombre.trim();
              }
              if (element.plan != undefined) {
                element.plan.forEach(elementE => {
                  elementE.nombre = elementE.nombre.trim();
                });
              }
            });

            return res.obraSocial
              .filter(x => x.nombre != undefined && x.nombre?.trim().length > 0)
              .sort((a, b) => {
                if (a.nombre > b.nombre) return 1;
                if (a.nombre < b.nombre) return -1;
                return 0;
            });
          }
      ));
    }
  }

  getObraSocial(codOS: string): Observable<ObraSocial> {
    console.log("getObraSocial " + codOS);
    if (this.useMockups) {
      return getWsFromMock(Mock.obraSocialMock1);
    } else {
      return this.http.get<AlertasObraSocialRespuesta>(this.endpoint_obraSocial + codOS)
        .pipe(map(
          (res: AlertasObraSocialRespuesta) => {
            throwErrorIfBadCode(res);
            res.obraSocial.nombre = res.obraSocial.nombre.trim();
            if (res.obraSocial.alertas == undefined) {
              res.obraSocial.alertas = [];
            } else {
              res.obraSocial.alertas.forEach(element => {
                element = element.trim();
              });
            }
            return res.obraSocial;
          }
      ));
    }
  }

  getEspecialidadesProfesionales(): Observable<EspecialidadProfesional[]> {
    if (this.useMockups) {
      return getWsFromMock(Mock.especialidadProfMock);
    } else {

      return this.http.get<EspecialidadProfesionalRespuesta>(this.endpoint_especialidadesProfesional)
        .pipe(map(
          (res: EspecialidadProfesionalRespuesta) => {
            throwErrorIfBadCode(res);
            res.especialidadProfesional.forEach(element => {
              if (element.nombreApellido != undefined) {
                element.nombreApellido = element.nombreApellido.trim();
              }
              if (element.especialidad != undefined) {
                element.especialidad.forEach(esp => {
                  esp.nombre = esp.nombre.trim();
                })
              }
            });

            var busqProf: EspecialidadProfesional = {
              codigo:0, 
              nombreApellido: "BÚSQUEDA AVANZADA",
              especialidad: undefined,
              horario: undefined,
              alertas: undefined,
              turnoParticular: undefined,
              turnoAM: undefined,
              turnoPM: undefined             
            };

            res.especialidadProfesional.push(busqProf);

            return res.especialidadProfesional.sort((a, b) => {
              if (a.codigo == 0) return -1;
              if (b.codigo == 0) return 1;
              if (a.nombreApellido > b.nombreApellido) return 1;
              if (a.nombreApellido < b.nombreApellido) return -1;
              return 0;
            });

          }
      ));
    }
  }

 
  getEspecialidadesProfesionalesFiltradas(os: string, esp:string): Observable<EspecialidadProfesionalBusq[]> {
    if (this.useMockups) {
      return getWsFromMock(Mock.especialidadProfFiltradasMock);
    } else {
      console.log("os " + os + " esp " + esp);

      // implementado como busquedaEspecialidades/244?esp=55&ca=1 y por post
      var endpointB = this.endpoint_busquedaEspecialidadesProfesional + '/' + os;
      if (esp != undefined) {
        endpointB = endpointB + '?esp=' + esp;
      }

      return this.http.post<EspecialidadProfesionalBusqRespuesta>(endpointB, null).pipe(map(
        (res: EspecialidadProfesionalBusqRespuesta) => {
          throwErrorIfBadCode(res);
          res.especialidadProfesional.forEach(element => {
            element.nombreApellido = element.nombreApellido?.trim();
            element.especialidad.nombre = element.especialidad.nombre?.trim();
            //element.turnoParticular
            //element.turnoAM
            //element.turnoPM
          });

          return res.especialidadProfesional;
        }
      ));
    }
  }
  
  getLicencias(codProf: string): Observable<Licencia[]> {
    if (this.useMockups) {
      return getWsFromMock(Mock.licenciasMock);
    } else {
      return this.http.get<LicenciaRespuesta>(this.endpoint_licencias + codProf)
      .pipe(map(
        (res: LicenciaRespuesta) => {
          throwErrorIfBadCode(res);

          res.licencia.forEach(element => {
            element.motivo = element.motivo.trim();
          });

          return res.licencia;
        }
      ));
    }
  }

  getTurnosFiltrados(filtrosTurnos: FiltrosTurnos): Observable<TurnoRespuesta> {
    if (this.useMockups) {
      if (filtrosTurnos.os === '12345') {
        return getWsFromMock(Mock.turnoRespuestaLic);
      } else {
        return getWsFromMock(Mock.turnoRespuesta);
      }
    } else {
      var endpoint = this.endpoint_turnos;
      
      if (filtrosTurnos.os != undefined || filtrosTurnos.ep != undefined || filtrosTurnos.fecha != undefined) {
        // ?os={codOS}&ep={codEP}&fecha={YYYY-MM-DD}  
        // en swagger vemos que está haciendo  turnos/416?codProf=115&fecha=2020-12-23
        
        //endpoint = endpoint + '?';
        var addAmp = false;
        if (filtrosTurnos.os != undefined) {
          //endpoint = endpoint + 'os=' + filtrosTurnos.os;
          //addAmp = true;
          endpoint = endpoint + "/" + filtrosTurnos.os;
        }
        endpoint = endpoint + '?';
        if (filtrosTurnos.ep != undefined) { 
          if (addAmp) {
            endpoint = endpoint + '&';
          }
          endpoint = endpoint + 'codProf=' + filtrosTurnos.ep;
          addAmp = true;
        }
        if (filtrosTurnos.fecha != undefined) {
          if (addAmp) {
            endpoint = endpoint + '&';
          }          
          endpoint = endpoint + 'fecha=' + filtrosTurnos.fecha;
        }
      }

      return this.http.get<TurnoRespuesta>(endpoint)
        .pipe(map(
          (res: TurnoRespuesta) => {
            throwErrorIfBadCode(res);
            if (res.licencia != undefined && res.licencia.motivo != undefined) {
              res.licencia.motivo = res.licencia.motivo.trim();
            }
            return res;
          }
      ));
    }
  }
  
  getPacientes(filtrosPacientes: FiltrosPacientes): Observable<Paciente[]> {
    console.log("getPacientes" + filtrosPacientes.dni);
    console.log((filtrosPacientes.dni == undefined || filtrosPacientes.dni.trim().length == 0));
    if (filtrosPacientes == undefined || 
      ( (filtrosPacientes.apenom == undefined || filtrosPacientes.apenom.trim().length == 0) && 
        (filtrosPacientes.dni == undefined || filtrosPacientes.dni.trim().length == 0)
      )
    ) {
        throw new Error(`Ingrese algun criterio de búsqueda`);
    }
    if (this.useMockups) {
      return getWsFromMock(Mock.pacientesList.paciente);

    } else {
      var endpoint =  this.endpoint_pacientes;
      
      if (filtrosPacientes.dni != undefined && filtrosPacientes.dni.trim().length > 0) {
        endpoint = endpoint + filtrosPacientes.dni.trim();
        if (filtrosPacientes.apenom != undefined && filtrosPacientes.apenom.trim().length > 0) {
          endpoint = endpoint + '?apenom=' + filtrosPacientes.apenom.trim();
        } 

      } else if (filtrosPacientes.apenom != undefined && filtrosPacientes.apenom.trim().length > 0) {
        endpoint = endpoint + filtrosPacientes.apenom.trim();
      } 

      return this.http.get<PacienteRespuesta>(endpoint).pipe(
        map((res: PacienteRespuesta) => {
          throwErrorIfBadCode(res);
          return res.paciente;
        })
      );
    }
  }

}

