import { createReducer, on } from '@ngrx/store';
import { CentroAtencion, Especialidad, EspecialidadProfesional, Licencia, ObraSocial, Paciente, Turno } from '../../../shared/models/datos.models';
import { FormularioTurno } from '../../../shared/models/forms.models';
import { TurnoRespuesta } from '../../../shared/models/responses.models';
import * as TurnoActions from '../actions/turno.actions';

export const initialState: 
    FormularioTurno = {
        turnoSelected: undefined,
        obraSocialSelected: undefined,
        datosPaciente: undefined,        
        pacienteSelected: undefined,
        pacientes: []
};

export function turnoReducer(state, action) {
    return _turnoReducer(state, action);
}

const _turnoReducer = createReducer(
    initialState,

    on(TurnoActions.clear, () => initialState),

    on(TurnoActions.setObraSocialSelected, (state, { obraSocialSelected }) =>
        _setObraSocialSelected(state, obraSocialSelected)),

    on(TurnoActions.setTurnoSelected, (state, { turnoSelected }) =>
        _setTurnoSelected(state, turnoSelected)),

    on(TurnoActions.setPacientes, (state, { pacientes }) =>
        _setPacientes(state, pacientes)),

    on(TurnoActions.setPacienteSelected, (state, { pacienteSelected }) =>
        _setPacienteSelected(state, pacienteSelected)),
);


const _setObraSocialSelected = 
(state: FormularioTurno, obraSocialSelected: ObraSocial): FormularioTurno => {
    const stateNew = { ...state };
    stateNew.obraSocialSelected = obraSocialSelected;
    return stateNew;
};

const _setTurnoSelected = 
(state: FormularioTurno, turno: Turno): FormularioTurno => {
    const stateNew = { ...state };
    stateNew.turnoSelected = turno;
    return stateNew;
};

const _setPacientes = (state: FormularioTurno, pacientes: Paciente[]): FormularioTurno => {
    const stateNew = { ...state };
    stateNew.pacientes = pacientes;
    return stateNew;
};

const _setPacienteSelected = (state: FormularioTurno, pacienteSelected: Paciente): FormularioTurno => {
    const stateNew = { ...state };
    stateNew.pacienteSelected = pacienteSelected;
    return stateNew;
};

